      <LuckyWheel ref="myLucky" :width="width" :height="height" :prizes="prizes" :blocks="blocks" :buttons="buttons"
        @start="startCallback" @end="endCallback" />
    </div>
    <var-button color="#69dbaa" text-color="#eee" class="set" @click="setLucky">设置</var-button>
    <var-action-sheet :actions="actions" :show.sync="show" @select="handleSelect" />
    <var-snackbar :show.sync="messageShow" type="success">
      {{ message }}</var-snackbar>
  </div>
