          name: "不中奖",
          icon: "heart-outline",
          color: "black",
          index: 0,
        },
        {
          name: "100元",
          icon: "heart-outline",
          color: "#00c48f",
          index: 1,
        },
        {
          name: "200元",
          icon: "heart-outline",
          color: "green",
          index: 2,
        },
        {
          name: "300元",
