  methods: {
    // 点击抽奖按钮会触发star回调
    startCallback() {
      if (!this.custom) {
        this.probability = Math.floor(Math.random() * this.prizes.length);
      }
      console.log(this.probability);
      // 调用抽奖组件的play方法开始游戏
      this.$refs.myLucky.play();
      this.$refs.myLucky.stop(this.probability);
    },
    endCallback(prize) {
      console.log(prize);
    },
    setLucky() {
      this.show = true;
    },
    // 选择事件
    handleSelect(val) {
      console.log(val);
      this.probability = val.index;
      this.show = false;
      this.message = `您已成功将奖品设置为${val.name}`;
      this.messageShow = true;
      this.custom = true;
    },
  },
  mounted() {
    this.actions = this.prizes.map((item, index) => {
      console.log(item)
      return {
        name: item.title,
        index,
        color: '#000'
      }
    })
    this.prizes = this.prizes.map(item => ({
      fonts: [{ text: item.title, top: '15%', fontColor: '#FF7002', fontSize: '0.426rem', fontWeight: 600, wordWrap: false, lineHeight: '0.613rem' }],
      imgs: [{ src: item.icon, top: '1.8rem', width: '1.3rem', height: '1.4rem' }]
    }))
    console.log('this.actions', this.actions)
  },
